.hire-us {
  background-color: #FFFFFF;
  background-image: url(../images/Shapes.png);
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  text-decoration: none;
  font-weight: 600;
  padding: 72px 20px;
  margin: 0;

  @include on-tablet {
    // background-size:cover;
    padding: 200px 65px 256px;
  }

  @include on-small-desktop {
    padding: 199px 65px 395px;
  }

  // &__block {

  //   @include on-tablet {
  //     background-repeat: no-repeat;
  //     background-position: center; // Центрує зображення
  //     // background-size: contain; // Зображення поміщається повністю
  //     background-image: url(../images/shapes.png);
  //     padding: 137px 0;
  //   }
  // }

  &__title {
    font-family: Poppins;
    font-size: 44px;
    line-height: 52.8px;
    color: #253757;
    margin: 0 0 24px;
  }

  &__subtitle {
    font-family: Open Sans;
    font-size: 22px;
    line-height: 33px;
    color: #6C788B;
    margin: 0 0 56px;

    @include on-tablet {
      margin: 0 0 64px;
    }
  }

  &__button-view {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    cursor: pointer;
    border: 0;
    border-radius: 8px;
    width:100%;
    height: 50px;
    font-family: Poppins;
    font-size: 15px;
    line-height: 22.5px;
    background-color: #2060F6;
    color: #FFFFFF;
    padding: 0;
    margin: 0;
    max-width:280px; /* Обмежуємо ширину контейнера */

    @media (min-width: 640px) {
      max-width:264px;
    }
  }
}
