.nav {
  display: block;
  // * { outline: 1px solid red;}

  &__menu {
    box-sizing: border-box;
    height: 100vh;
    width: 100%;
    color: $main-text-color;
    font-weight: 700; /* жирність літер тексту */
    text-transform: uppercase; /* щоб лiтери стали великими */
    background-size: cover;
  }

  &__list {
    display: flex;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    gap: 48px;

    @include on-small-desktop {
      margin-left: 48px;
    }

    @media all and (min-width: 1280px) {
        margin-left: 58px;
    }

    @media all and (min-width: 1600px) {
      margin-left: 151px;
    }
  }

  &__item {
    display: none;
  }

  @media (min-width: 1024px) { /* Для ПК */
    &__item {
        display: block; /* Отображаем текст */
    }
  }

  &__link {
    display: block;
    content: '';
    position: relative;
    color: inherit; /* успадкований колір текста */
    text-decoration: none; /* прибирає підкреслення і стилі з посилань */
    font-family: Poppins;
    font-weight: 700;
    font-size: 13px;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-align: center;
    white-space: nowrap;

    &::after {
      content: '';
      position: absolute;
      display: block;
      bottom: -8px;
      height: 1px;
      width: 100%;
      background-color: #2060F6;
      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }

  &__hire-us {
    font-family: Poppins;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-align: center;
    color: #FFFFFF;
    text-decoration: none;
    margin-left: auto;

    @include on-small-desktop {
      padding-inline: 54px;
    }

    @include on-desktop {
      padding-inline: 24px;
    }

  }
}

