.services {
  @include page-grid;
  position: relative;

  padding-inline: 20px;
  padding-block: 120px 120px;
  gap: 24px;

  @include on-tablet {
    padding-inline: 41px;
    padding-block: 148px 148px;
    gap: 30px;
  }

  @include on-small-desktop {
    padding-inline: 54px;
    padding-block: 151px 148px;
  }

  @include on-the-design {
    padding-inline: 227px;
  }

  @include on-large-screens {
    padding-inline: 707px;
  }

  &__title {
    color: #C0CDD7;
    font-family: Poppins;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    margin: 0 0 16px;
    text-align: left;
    text-decoration: none;
  }

  &__title-section {
    color: #253757;
    font-family: Poppins;
    font-size: 32px;
    font-weight: 600;
    line-height: 41.6px;
    margin: 0 0 24px;
    text-align: left;
    text-decoration: none;
  }

  &__text {
    color: #6C788B;
    font-family: Open Sans;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    margin: 0 0 24px;
    text-align: left;
    text-decoration: none;

    @include on-tablet {
      margin: 0 0 26px;
    }

    // @include on-small-desktop {
    //   margin: 0 0 287px;
    // }
  }

  &__section-inform {
    padding-inline: 0;
    padding-block: 0;
    display: block;
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-small-desktop {
      grid-column: 1 / 5;
    }
  }

  &__cards {
    @include page-grid;
    padding-inline: 0;
    padding-block: 0;
    grid-column: 1 / -1;
    margin-top: -10%;
    gap: 24px;

    @include on-tablet {
      display: flex;
      flex-wrap: wrap;
      position: static;
      top: 48px;
      left: 49%;
      width: 558px;
      gap: 30px;
    }


    @include on-small-desktop{
      display: flex;
      flex-wrap: wrap;
      // position: absolute;
      grid-column: 7 / 12;

      top: 48px;
      left: 49%;
      width: 558px;
      gap: 30px;
    }
  }

  &__card-left {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-small-desktop {
      grid-column: 7 / 10;
    }
  }

  &__card-right {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 4 / 7;
      grid-row: 0;
    }

    @include on-small-desktop {
      grid-column: 10 / 13;
    }
  }

  &__card {
    display: block;
    box-shadow: 0px 9px 18px 0px #25293108;
    background: #FFFFFF;
    border-radius: 16px;
    padding: 32px;
  }

  &__card--1 {
    @media (min-width: 640px){
      margin-top: 139px;
      transition: all 0.5s ease-in;
      // margin: 384px 0 0;
      // transform: trasnlateY(-50%);
      grid-column: 1 / 3;
    }

  }

  &__card--2 {
    @media (min-width: 640px){
      margin-bottom: 139px;
      transition: all 0.5s ease-in;
      // margin: 384px 0 0;
      // transform: trasnlateY(-50%);
      grid-column: 4 / 6;
    }
  }

  &__card--3 {
    @media (min-width: 640px){
      transition: all 0.5s ease-in;
      // margin: 384px 0 0;
      // transform: trasnlateY(-50%);
      grid-column: 1 / 3;
    }
  }

  &__card--4 {
    @media (min-width: 640px){
      margin-top: -139px;
      height: fit-content;
      transition: all 0.5s ease-in;
      // margin: 384px 0 0;
      // transform: trasnlateY(-50%);
      grid-column: 4 / 6;
    }
  }

  &__image-number {
    margin-bottom: 24px;
  }

  &__text-block {
    color:#6C788B;
    font-family: Open Sans;
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
    text-align: left;
    text-decoration-skip-ink: none;
    margin: 0 0 32px;
  }

  &__arrow-learn-more {
    display: flex;
    gap: 16px;
    align-items: center;
    justify-content: flex-start;
  }

  &__image-arrow {
    flex: 1;
  }

  &__learn-more {
    flex: 1;
    color:#334563;
    font-family: Poppins;
    font-size: 15px;
    font-weight: 600;
    line-height: 22.5px;
    text-decoration: none;
    margin: 0;
  }
}
