.icon {
  background-size: cover;
  background-position: 50%;
  height: 24px;
  width: 24px;
  transition: transform 0.3s;

  @include hover(transform, scale(1.2));

  &--menu {
    background-image: url(../images/icons/burger-menu.svg);
  }

  @media (min-width: 1024px) { /* Для ПК */
    &--menu {
        display: none;
    }
  }
}
