.header {
  // height: 100px;


  &__top,
  &__bottom,
  &__title-on-the-image,
  &__subtitle-on-the-image {
    padding-block: 20px 0;
    padding-inline: 20px;

    @include on-tablet {
      padding-inline: 41px;
    }

    @include on-small-desktop {
      padding-block: 48px 0;
      padding-inline: 54px;
    }

    @include on-desktop {
      padding-left: 227px;
    }

    @include on-the-design {
      padding-inline: 227px;
    }

    @include on-large-screens {
      padding-left: 707px;
    }
  }

  &__top{
    display: flex;
    flex-direction: row;
    height: 100px;
    gap: 32px;
  }

  &__content {
    @media (min-width: 1024px){
      @include page-grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  &__bottom {
    text-align: left;
    color: #FFF;

    @media (min-width: 1024px) {
      padding-block: 120px 0;
    }

    @media (min-width: 1280px) {
      padding-left: 227px 0;
    }
  }

&__title {
  font-family: Poppins;
  font-size: 52px;
  font-weight: 600;
  line-height: 62.4px;
  letter-spacing: -2px;
  margin: 0;

  @media (min-width: 640px) {
    font-size: 64px;
    line-height: 68px;
  }
}

  &__subtitle {
    font-family: Open Sans;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    margin: 32px 0 0;
    text-align: left;

    @media (min-width: 640px) {
      max-width:370px; /* Обмежуємо ширину контейнера */
      word-wrap: break-word; /* Переносимо довгі слова */
      white-space: normal;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    gap: 0;
    border: 0;
    border-radius: 8px;
    width:100%;
    height: 50px;
    cursor: pointer;
    font-family: Poppins;
    font-size: 15px;
    font-weight: 600;
    line-height: 22.5px;
    text-align: center;
    background: #2060F6;
    color: var(--main-text-color);
    text-decoration: none;
    margin: 72px 0 0;
    padding: 0;

    @media (min-width: 640px) {
      max-width:269px; /* Обмежуємо ширину контейнера */
      word-wrap: break-word; /* Переносимо довгі слова */
      white-space: normal;
      margin: 72px 0 0;
    }

    @media (min-width: 1024px) {
      margin: 72px 0 0;
    }
  }

  // &__image-header {
  //   position: relative;
  //   padding-block: 0;
  //   padding-inline: 0;
  // }

  &__view-photo {
    position: relative;
    object-fit: cover;
    background-size: cover;
    background-position: center;
    background-image: url('../images/header-title/header-bg-mobile.png');
    width: 100%;
    height: 64vh;
    border-radius: 30px;
    margin: 48px 0 0;

    @media (min-width: 640px){
      background-image: url('../images/header-title/header-bg-large-screens.png');
      margin: 147px 0 0;
      height: 64vh;
    }

    @media (min-width: 1024px) {
      background-image: url('../images/header-title/header-bg-mobile.png');
      border-radius: 30px 0px 30px 0px;
      margin: 0;
      height: 54vh;
      width: 100%;
    }

    @media (min-width: 1280px) {
      background-image: url('../images/header-title/header-bg-large-screens.png');
      border-radius: 30px 0px 30px 0px;
      height: 48vh;
      width: 100%;
    }

    @media (min-width: 2560px) {
      background-image: url('../images/header-title/header-bg-2560.png');
      border-radius: 30px 0px 30px 0px;
      height: 30vh;
      width: 100%;
    }
  }

  &__arrow-left,
  &__arrow-right {
    position: absolute;
    top: 70%; // Вертикально по центру
    transform: translateY(-80%); // Центруємо по вертикалі
    width: 32px;
    height: auto;
    z-index: 10; // Задаємо вищий рівень для стрілок

    @media (min-width: 640px){
      top: 68%; // Вертикально по центру
      transform: translateY(-80%); // Центруємо по вертикалі
    }

    @media (min-width: 1024px) {
      top:90%; // Вертикально по центру
      transform: translateY(-80%); // Центруємо по вертикалі
    }
  }

  &__arrow-left {
    // margin-left: 20px;  /* Лівий відступ від краю */
    // margin-top: 250px;
    left: 20px;

    @media (min-width: 640px){
      left: 41px;
    }

    @media (min-width: 1024px){
      left: 78px;
    }
  }

  &__arrow-right {
    // margin-right: 220px;  /* Правий відступ від краю */
    // margin-top: 250px;

    left: 68px;

    @media (min-width: 640px){
      left: 89px;
    }

    @media (min-width: 1024px){
      left: 126px;
    }
  }

  &__title-on-the-image {
    position: absolute;
    top: 72%; // Центруємо по вертикалі
    transform: translateY(-10%); // Центруємо
    margin: 0;
    font-family: Poppins;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-align: center;
    color: #FFFFFF;

    @media (min-width: 640px){
      top: 70%; // Центруємо по вертикалі
      transform: translateY(-10%); // Центруємо
    }

    @media (min-width: 1024px) {
      top: 76%; // Центруємо по вертикалі
      transform: translateX(80%); // Центруємо
    }

    @media (min-width: 1280px) {
      top: 74%; // Центруємо по вертикалі
      transform: translateX(-10%); // Центруємо
    }

    @media (min-width: 1600px) {
      top: 74%; // Центруємо по вертикалі
      transform: translateX(-7%); // Центруємо
    }

    @media (min-width: 2560px) {
      top: 76%; // Центруємо по вертикалі
      transform: translateX(-52%); // Центруємо
    }
  }

  &__subtitle-on-the-image {
    position: absolute;
    margin: 0;
    top: 82%; // Центруємо по вертикалі
    transform: translateY(-20%); // Центруємо
    font-family: Open Sans;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    text-align: left;
    color: #FFFFFF;

    @media (min-width: 640px) {
      top: 80%; // Центруємо по вертикалі
      left: 48%;
      transform: translate(-100%, -20%); // Центруємо
    }

    @media (min-width: 1024px) {
      top: 83%; // Центруємо по вертикалі
      left: 30%;
      transform: translate(-8%, -20%); // Центруємо
    }

    @media (min-width: 1280px) {
      top: 86%; // Центруємо по вертикалі
      left: 10%;
      transform: translate(-20%, -40%); // Центруємо
    }

    @media (min-width: 1600px) {
      top: 74%; // Центруємо по вертикалі
      left: 4%;
      transform: translate(-10%, 40%); // Центруємо
    }

    @media (min-width: 2560px) {
      top: 78%; // Центруємо по вертикалі
      left: -50%;
      transform: translate(10%, 20%); // Центруємо
    }
  }
}

.hire__us.is-active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 2px;
  background-color: #2060F6;
}

