
.contacts {
  color: #FFFFFF;
  background-color: #2C2C2C;
  text-decoration: none;
  text-align: left;
  font-weight: 600;
  border-radius: 30px 30px 0 0;

  padding-block: 72px 80px;
  padding-inline: 20px;
  margin: 120px 0 0 0;

  // @include page-grid;

  @include on-tablet {
    padding-block: 180px 139px;
    padding-inline: 41px;
    margin: 0;
  }

  @include on-small-desktop {
    padding-block: 180px 0;
    padding-inline: 54px;
  }

  // @include on-desktop {
  //   padding-block: 48px 0;
  //   padding-inline: 54px;
  // }

  @include on-the-design {
    padding-inline: 227px;
  }

  @include on-large-screens {
    padding-inline: 707px;
  }

  &__contact {
    grid-column: 1 / -1;
    @include on-tablet {
      grid-column: 1 / 5;
    }

    @include on-small-desktop {
      @include page-grid;
      grid-column: 1 / 6;
    }
  }

  &__title-send-us {
    font-family: Poppins;
    font-size: 32px;
    line-height: 41.6px;
    margin: 0 0 48px 0;

    @include on-tablet {
      line-height: 48px;
      margin: 0 0 56px 0;
    }
  }

  &__send-us {
    margin-bottom: 80px;

    @include on-tablet {
      margin-bottom: 120px;
    }

    @include on-small-desktop {
      grid-column: 1 / 6;
      width: 100%;
    }
  }

  &__forms {
    background: #2C2C2C;
    font-family: Poppins;
    font-size: 15px;
    line-height: 22.5px;
    margin-bottom: 16px;

    @include on-tablet {
      margin-bottom: 32px;
    }
  }


  &__form {
    width: 100%;
    height: 41px;
    gap: 0;
    border: 0;
    color: $main-text-color;
    font-family: Poppins;
    font-size: 15px;
    line-height: 22.5px;
    background-color:  #2C2C2C;
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    padding-bottom: 17px;
    outline: none; /* Видаляє контур фокусу */
    border-bottom: 1px solid $main-text-color;

    &:not(:placeholder-shown) {
      color: #FFFFFF;
    }

    &:focus {
      border-bottom: 2px solid #144ED4;
    }

    @include on-tablet {
      padding-bottom: 16px;
      margin-bottom: 48px;
    }
  }

  &__button-send {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    cursor: pointer;
    gap: 0;
    border: 0;
    border-radius: 8px;
    width:100%;
    height: 56px;
    font-family: Poppins;
    font-size: 15px;
    line-height: 22.5px;
    background-color: #144ED4;
    color: #FFFFFF;
    padding: 0;
    margin: 0;

    @media (min-width: 640px) {
      display: flex; /* Включає flexbox */
      max-width:269px; /* Обмежуємо ширину контейнера */
      word-wrap: break-word; /* Переносимо довгі слова */
      white-space: normal;
      justify-content: center;
      align-items: center;
    }
  }

  &__contact-us {
    @include on-small-desktop {
      grid-column: 7 / 12;
      width: 100%;
  }
  }

  &__title-contact-us {
    font-family: Poppins;
    font-size: 32px;
    line-height: 48px;
    margin: 0 0 8px 0;

    @include on-small-desktop {
      grid-column: 7 / 12;
    }
  }

  &__name {
    font-family: Poppins;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    color: #FFFFFF;
    opacity: 50%;
    margin: 40px 0 8px 0;

    @include on-tablet {
      margin: 48px 0 8px 0;
    }
  }

  &__number-phone,
  &__address  {
    color: #FFFFFF;
    text-decoration: none;
    font-family: Open Sans;
    font-size: 24px;
    font-weight: 600;
    line-height: 36px;
  }

  &__image-logo {
    padding-top: 8px;
    gap: 32px;
  }

  &__image {
    color: #FFFFFF;
    padding-right: 32px;
    transition: color 0.3s ease;
    cursor: pointer;
  }

  &__image:hover {
    color: #2060F6;
  }
}

.contacts__form.is-active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 2px;
  background-color: #2060F6;
}
