.testimonials {
  // position: relative;

  padding-inline: 20px;
  padding-block: 0 120px;
  gap: 24px;

  @include on-tablet {
    padding-inline: 41px;
    padding-block: 0 148px;
    gap: 30px;
  }

  @include on-small-desktop {
    padding-inline: 54px;
    padding-block: 0 186px;
    gap: 20px;
  }

  @include on-the-design {
    padding-inline: 227px;
    padding-block: 0 148px;
  }

  @include on-large-screens {
    padding-inline: 707px;
  }

  &__blocks-title{
    display: grid;
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 4;

      .move-to-new-line {
        display: block;
      }
    }
  }

  &__title {
    font-family: Poppins;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    color: #C0CDD7;
    margin: 0 0 16px 0;
  }

  &__title-section {
    font-family: Poppins;
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
    text-align: left;
    text-decoration: none;
    color:  #253757;
    margin: 0 0 24px 0;


    @include on-tablet {
      margin: 0 0 26px 0;
    }

    @include on-small-desktop {
      margin: 0 0 61px 0;
    }
  }

  &__blocks-block {
    display: grid;
    gap: 24px;
    grid-column: 1 / -1;

    @include on-tablet {
      gap: 30px;
    }

    @include on-small-desktop {
      @include page-grid;
      grid-template-columns: repeat(12, 1fr);
      // width: 100vw;
      gap: 20px;
    }
  }

  &__blocks {
    box-shadow: 0px 9px 18px 0px #25293108;
    background: #FFFFFF;
    border-radius: 16px;

    padding-inline: 42px;
    padding-block: 32px 32px;

    @include on-tablet {
      padding-inline: 98px;
    }

    @include on-small-desktop {
      padding-inline: 48px;
    }
  }

  &__blocks-1 {
    @include on-small-desktop {
      grid-column: span 4;
    }
  }
  &__blocks-2 {
    @include on-small-desktop {
      grid-column: span 4;
    }
  }
  &__blocks-3 {
    @include on-small-desktop {
      grid-column: span 4;
    }
  }

  &__photo-ava {
    display: block;
    margin: 0 auto 24px;
  }

  &__photo-quote {
    display: block;
    margin: 0 auto 8px;

    @include on-tablet {
      margin: 0 auto 16px;
    }

    @include on-small-desktop {
      margin: 0 auto 8px;
    }
  }

  &__text {
    font-family: Open Sans;
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
    text-align: center;
    text-decoration: none;
    color:  #6C788B;
    margin: 0 0 16px;
  }

  &__name {
    font-family: Open Sans;
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
    text-align: center;
    text-decoration: none;
    color: #253757;
    margin: 0 0 4px;
  }

  &__job-title {
    font-family: Poppins;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-align: center;
    text-decoration: none;
    color:  #C0CDD7;
    margin: 0;
  }
}
