.footer {
  display: flex;

  color: #FFFFFF;
  background-color: #2C2C2C;
  text-decoration: none;
  text-align: left;


  font-weight: 600;

  padding-bottom: 56px;
  padding-inline: 20px;

  @include on-tablet {
    padding-bottom: 80px;
    padding-inline: 41px;
  }

  @include on-small-desktop {
    padding-block: 0 80px;
    padding-inline: 54px;
  }

  // @include on-desktop {
  //   padding-block: 48px 0;
  //   padding-inline: 54px;
  // }

  @include on-the-design {
    padding-inline: 227px;
  }

  @include on-large-screens {
    padding-inline: 707px;
  }

  &__block{
    @include on-small-desktop {
      display: flex;
      justify-content: space-between; /* розсунути елементи */
      align-items: center; /* центування елементів по лінії */
      width: 100%;
    }
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    width: 44.82px;
    height: 25.63px;

    @include hover(transform, scale(1.2));
  }

  &__nav {
    display: block;

    &-menu {
      box-sizing: border-box;
      color: $main-text-color;
      font-weight: 700; /* жирність літер тексту */
      text-transform: uppercase; /* щоб лiтери стали великими */
      background-size: cover;
    }

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
      width: 100%;
      margin-top: 48px;

      @include on-tablet {
        display: flex;
        margin-top: 24px;
        gap: 40px;
      }

      @include on-small-desktop {
        margin-left: 48px;
      }

      @media all and (min-width: 1280px) {
          margin-left: 54px;
      }

      @media all and (min-width: 1600px) {
        margin-left: 151px;
      }
    }

    &-item {
      margin-bottom: 24px;

      @include on-tablet {
        margin: 0;
      }
    }

    &-link {
      display: block;
      content: '';
      position: relative;
      color: inherit; /* успадкований колір текста */
      text-decoration: none;
      font-family: Poppins;
      font-weight: 700;
      font-size: 13px;
      line-height: 19.5px;
      letter-spacing: 3px;
      list-style: none;
      text-align: left;
      white-space: nowrap;

      &::after {
        content: '';
        position: absolute;
        display: block;
        bottom: -8px;
        height: 1px;
        width: 100%;
        background-color: #2060F6;
        transition: transform 0.3s;
        transform: scale(0);
        transform-origin: left;
      }

      &:hover::after {
        transform: scale(1);
      }
    }
  }
}
