.our-expertise {

  background-color: #FFFFFF;
  border-radius: 30px;
  text-align: center;

  padding-inline: 20px;
  padding-block: 72px 72px;

  @include on-tablet {
    padding-inline: 41px;
    padding-block: 128px 128px;
  }

  @include on-small-desktop {
    padding-inline: 54px;
    padding-block: 128px 134px;
    gap: 30px;
  }

  @include on-the-design {
    padding-inline: 227px;
  }

  @include on-large-screens {
    padding-inline: 707px;
  }

  &__title {
    font-family: Poppins;
    font-size: 44px;
    font-weight: 600;
    line-height: 52.8px;
    text-align: center;
    text-decoration: none;
    color: #253757;
    margin:0 0 56px;

    @include on-tablet {
      margin:0 0 64px;
    }
  }

  &__list{
    @include page-grid;
      row-gap: 56px;

    @include on-tablet {
      row-gap: 72px;
    }

    // @include on-small-desktop {
    //   column-gap: 137px;
    // }

    transition: transform 0.3s ease; /* Smooth transition effect */

    :hover img {
      transform: scale(1.1); /* Enlarge the image on hover */
    }
  }

  &__item {
    overflow: hidden;/* Заборона виходу за межі контейнера */

    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-small-desktop {
      grid-column: span 4;
    }
  }

  // &__item-branding {
  //   @include on-small-desktop {
  //     float: left;
  //     grid-column: 1 / 4;
  //   }
  // }

  // &__item-communication {
  //   @include on-small-desktop {
  //     grid-column: 6 / 9;
  //   }
  // }

  // &__item-stategy {
  //   @include on-small-desktop {
  //     float: right;
  //     grid-column: 10 / 13;
  //   }
  // }



  &__photo {
    display: flex;
    justify-content: center; /* Центрування по горизонталі */
    align-items: center;     /* Центрування по вертикалі */
    width: 100%;
    margin-bottom: 32px;

    @include on-small-desktop {
      margin-bottom: 40px;
    }

    &-view {
      max-width: 100%;  /* Зображення не виходить за межі контейнера */
      height: auto;     /* Зберігає пропорції зображення */
      border-radius: 30px;
    }
  }

  &__photo-1{
    display: flex;
    justify-content: center; /* Центрування по горизонталі */
    align-items: center;     /* Центрування по вертикалі */
    padding: 0;

    width: 100%;
    margin-bottom: 32px;

    @include on-small-desktop {
      margin-bottom: 40px;
    }
  }

  &__subtitle{
    font-family: Poppins;
    font-size: 20px;
    font-weight: 600;
    line-height: 28px;
    text-align: center;
    text-decoration: none;
    color: #253757;
    margin: 0 0 16px;
  }

  &__text{
    font-family: Open Sans;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-align: center;
    text-decoration: none;
    color: #6C788B;
    margin: 0;
  }
}
