.about-us {
  // display: flex;
  flex-wrap: wrap; /* Переносить елементи на новий рядок */
  justify-content: center;

  padding-block: 120px 120px;
  padding-inline: 20px;


  @include on-tablet {
    padding-block: 148px 148px;
    padding-inline: 41px;
  }


  @include on-small-desktop {
    // padding-block: 48px 0;
    padding-inline: 54px;
    margin: 0 100px 0;
  }

  // @include on-desktop {
  //   padding-block: 48px 0;
  //   padding-inline: 54px;
  // }

  // @include on-the-design {
  //   // padding-block: 48px 0;
  //   padding-inline: 227px;
  // }

  @include on-large-screens {
    // padding-block: 48px 0;
    padding-inline: 707px;
  }


  &__title {
    font-family: Poppins;
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
    text-align: center;
    text-decoration: none;
    color: #253757;
    margin: 0;
    margin-bottom: 24px;

    @media (min-width: 640px){
      margin-bottom: 16px;
    }
  }

  &__text {
    font-family: Open Sans;
    font-size: 22px;
    font-weight: 600;
    line-height: 33px;
    text-align: center;
    text-decoration: none;
    color: #6C788B;
    margin: 0;
  }
}
